body {
  font-family: sans-serif;
  margin: 0;
  padding: 20px;
  display: flex;
  justify-content: center;
  background-color: #f0f2f5;
}

.contenedor-horizontal {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  align-items: flex-start;
  justify-content: flex-start;
}

.info-dispositivo {
  width: 300px;
  background: #ffffff;
  padding: 15px 20px;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.08);
  flex-shrink: 0;
}

.imagen-dispositivo {
  max-width: 400px;
  max-height: 300px;
  object-fit: contain;
  border-radius: 12px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.galeria-fotos {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.fotos-adicionales {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}
